import {
	mapState,
	mapMutations
} from 'vuex';
import store from "../../../store/index.js";
import api from "../../../api/api.js";
export default {
	computed: mapState(['hasLogin', 'userInfo', 'token']),
	data() {
		return {}
	},
	methods: {
		...mapMutations(['logout']),
		logoOut() {
			if (this.hasLogin) {
				uni.showToast({
					icon: 'none',
					title: '注销成功',
				});
				this.logout();
				uni.navigateBack();
			} else {
				uni.showToast({
					icon: 'none',
					title: '您尚未登录哟~',
				});
			}
		},
		gotoAccountSecurity() {
			if (this.hasLogin) {
				uni.navigateTo({
					url: '../../../pages/my/setting/account-security/account-security'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '您尚未登录哟~',
				});
			}
		}
	}
}
